@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.SkillsLevel {
  display: flex;
  gap: 1px;
  align-items: center;
  justify-content: flex-end;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 8px;
  margin: 4px;

  &Marker {
    background-color: $light-gray-color;
    width: 30px;
    height: 15px;
    opacity: 0.8;

    //@media (max-width: $medium-size) {
    //  display: none;
    //}

    &Hover {
      background-color: $light-gray-color;
      opacity: 1;
    }

    &Active {
      background-color: $yellow-color;
      opacity: 1;
    }

    &Empty {
      background-color: $light-gray-color;
      opacity: 0.3;
    }
  }
}
