@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Technology {
  background-color: #F0F0F0;
  padding: 0 12px;
  border-radius: 12px;
  margin: 0 3px;
  cursor: default;
  vertical-align: baseline;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  align-items: center;
  gap: 3px;

  @media (max-width: $medium-size) {
    padding: 0 6px;
    margin: 0 2px;
    gap: 2px;
  }

  &:hover {
    background-color: $yellow-color;
  }

  &Tooltip {
    padding: 40px;

    &Content {
      display: grid;
      grid-template-columns: max-content 1fr;
      gap: 10px;
      align-items: center;

      &Level {
        grid-column: 1/3;
        display: flex;
        justify-content: center;
      }
    }

    &Arrow {

    }
  }
}
