@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.SkillsList {
  list-style-type: disc;

  @media (max-width: $medium-size) {
    list-style-type: none;
    padding-left: 15px;
  }

  ::marker {
    color: $light-gray-color;
    font-size: 0.5rem;
  }

  &Item {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 42px;

    @media (max-width: $medium-size) {
      font-weight: 400;
      font-size: 15px;
      line-height: 34px;
    }

    &Hoverable {
      &:hover {
        background-color: #FAFAFA;
      }
    }

    &Text {
      display: flex;
      flex-wrap: wrap;
    }

    &Level {
      @media (max-width: $medium-size) {
        display: none;
      }
    }

    &Name {
      font-weight: 600;
    }
  }

  &WithLevel {
    display: grid;
    grid-template-columns: 1fr auto;
  }
}
